.menu-item {
  background: #eeeeee;
  border-radius: 50%;
  width: 110px;
  height: 110px;
  margin-left: -40px;
  position: absolute;
  color: #ffffff;
  text-align: center;
  line-height: 80px;
  transform: translate(0, 0);
  transition: transform ease 0.5s;
}

.menu-open-button {
  background: #4472c4;
  border-radius: 50%;
  width: 95px;
  height: 95px;
  margin-left: -40px;
  position: absolute;
  color: #ffffff;
  text-align: center;
  line-height: 80px;
}

.menu-open {
  display: none;
}

.menu-open-button {
  z-index: 10;
  cursor: pointer;
}

.menu {
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 80px;
  height: 80px;
  text-align: center;
  box-sizing: border-box;
  font-size: 26px;
}

.menu > .menu-item {
  z-index: 5;
  transition-duration: 0.5s;
}

.item-1 {
  background-color: #f98435;
}

.item-1:hover {
  cursor: pointer;
}

.item-1::after {
  content: "";
  position: absolute;
  top: 100%;
  left: 50%;
  width: 0;
  height: 0;
  background: #f98435;
  transition: all 0.5s ease;
  transform: rotate(0deg);
  transform-origin: 0% 0%;
}

.menu-open:checked ~ .item-1::after {
  width: 2px;
  height: 110px;
}

