* {
  margin: 0;
  padding: 0;
}

ul li {
  list-style-type: none;
  padding-top: 4px;
}

.cPadding {
  /* padding: 0 3% !important; */
  padding-left: 25px;
  padding-right: 25px;
}

.addCode:before {
  content: "\f055";
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-decoration: inherit;
  font-size: 24px;
  color: #2c6ddd;
}

.addedCode:before {
  content: "\f058";
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-decoration: inherit;
  font-size: 24px;
  color: rgb(87, 206, 87);
}

.scrollable {
  height: 150px; /* or any value */
  overflow-y: auto;
}

/* width */
.scrollable::-webkit-scrollbar {
  width: 8px;
}

/* Track */
.scrollable::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px #e6e6e6;
  border-radius: 5px;
  background-color: #e6e6e6;
}

/* Handle */
.scrollable::-webkit-scrollbar-thumb {
  background: #bdbaba;
  border-radius: 5px;
}

/* Handle on hover */
.scrollable::-webkit-scrollbar-thumb:hover {
  background: #bbb5b5;
}

.img-style {
  max-width: 65px;
  max-height: 65px;
}

.icon-style {
  width: 10px;
  height: 10px;
}
.icon-view {
  width: 15px;
  height: 10px;
}
