.subheader div:first-of-type > h4 {
  color: #fff;
  margin-left: 20px;
  font-weight: 900;
  padding-top: 16px;
  padding-bottom: 5px;
}

.subheader div {
  width: 50%;
}

.depositSlipImg {
  margin-bottom: 10px;
  height: 300px;
  width: 450px;
  border-radius: 15px;
  padding: 0px;
  line-height: 1.42857143;
  background-color: #f7f7f7;
  border: 1px solid #e2dbd6;
  transition: all 0.2s ease-in-out;
  display: inline-block;
}

@import url("//maxcdn.bootstrapcdn.com/font-awesome/4.1.0/css/font-awesome.min.css");
.search {
  position: relative;
}
.search input {
  text-indent: 32px;
}
.search .fa-search {
  position: absolute;
  top: 20px;
  left: 10px;
  font-size: 15px;
}
