@media screen and (max-width: 768px) {
  .subheader div {
    width: 50%;
    align-items: center;
    display: flex;
  }
  .subheader div:first-of-type > h4 {
    font-size: 18px;
    margin-left: 10px;
  }
  .depositSlipImg {
    height: 250px;
    width: 250px;
  }
}

@media screen and (max-width: 600px) {
  .subheader div:first-of-type > h4 {
    margin-left: 5px;
    font-weight: 700;
    padding-top: 8px;
    font-size: 16px;
  }
  .subheader div {
    width: 23%;
    align-items: center;
    display: flex;
  }
  .cPadding {
    padding: 0 5% !important;
  }
  .img-style {
    max-width: 55px;
    max-height: 60px;
  }
  .depositSlipImg {
    height: 230px;
    width: 210px;
  }
}

/* css  for radio tabs */
input[type="radio"] {
  box-shadow: none;
}
